import React from 'react';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css'

class ParaDropdown extends React.Component{

  constructor(props){
    super(props);
    this.state = {id:this.props.id,value:null};
    this._onSelect = this._onSelect.bind(this)
  }

  render(){
    const options = this.props.options;
    const defaultOption =this.props.defaultOption;
    return <Dropdown options={options} onChange={this._onSelect} value= {defaultOption} placeholder="Select an option" />
  }

  _onSelect(choice){
    this.value = choice.value;
    let new_state = this.state;
    new_state.value = choice.value;
    this.setState(new_state);
    this.props.callbackFromParent(this.state);
  }

}


export default ParaDropdown;



