import React from 'react';
import * as d3 from 'd3';

class Scatterplot extends React.Component{

  constructor(props){
    super(props);
    this.state = {id:this.props.id,value:null};
  }

  componentDidMount()
  {
    if(this.props.data){
      this.draw();
    }
  }

  componentDidUpdate()
  {
    if(this.props.data){
      this.draw();
    }
  }

  draw()
  {
    // Data for Scatterplot
    // Modify : To accomodate the different kinds of data types.
    let scatterplot_data = [];
    this.props.data.forEach(d=>{
      let temp_data = {
        x:parseFloat(d[this.props.parameter[0]])?parseFloat(d[this.props.parameter[0]]):0,
        y:parseFloat(d[this.props.parameter[1]])?parseFloat(d[this.props.parameter[1]]):0,
        other:d
      }
      scatterplot_data.push(temp_data);
    })

    // Initializing Histogram Parameter
    const scatterplotMargin = {top:100, bottom:50, right:100, left:50};
    const width = this.props.width - (scatterplotMargin.left+scatterplotMargin.right);
    const height = this.props.height - (scatterplotMargin.top+scatterplotMargin.bottom);

    // Modify : To accomodate the different kinds of data types.
    const xScale = d3.scaleLinear()
                    .rangeRound([0,width])
                    .domain(d3.extent(scatterplot_data,d=>{return d.x}));

    const yScale = d3.scaleLinear()
                    .rangeRound([height,0])
                    .domain(d3.extent(scatterplot_data,d=>{return d.y}));

    // Drawing the scatterplot
    const scatterplotContainer = d3.select("#"+this.props.id);

    // Clean up contents of container
    scatterplotContainer.select("#Scatter").remove();

    const innerScatterContainer = scatterplotContainer.append("g")
                        .attr("id","Scatter")
                        .attr("transform","translate("+scatterplotMargin.left+","+scatterplotMargin.top+")")

    // Title
    innerScatterContainer.append("text")
                          .attr("id","title")
                          .text(this.props.parameter[0]+" Vs " + this.props.parameter[1])
                          .attr("transform","translate(-10,-50)");

    //ToolTip
    var tooltip = d3.select("#ScatterplotContainer").append("div")
                  .attr("class", "tooltip")
                  .style("opacity", 0);

    // Tooltip mouseover event handler
    var para1 = this.props.parameter[0];
    var para2 = this.props.parameter[1];
    var tipMouseover = function(d) {
         var html  = d.other['P. Name'] + "<br/>" +
                     "<span><b>" + para1 +"</b> : "+ d.other[para1]+ "</span><br/>" +
                     "<span><b>" + para2 +"</b> : "+ d.other[para2];

         tooltip.html(html)
             .style("left", (d3.event.pageX + 15) + "px")
             .style("top", (d3.event.pageY - 28) + "px")
             .transition()
             .duration(200)
             .style("opacity", .9)
     };

     // Tooltip mouseout event handler
     var tipMouseout = function(d) {
         tooltip.transition()
             .duration(300)
             .style("opacity", 0);
     };

    //Add elements to the scatterplot
    const each_circle = innerScatterContainer.append("g")
                          .attr("class","Circles")
                          .selectAll("circle")
                          .data(scatterplot_data)
                          .enter()
                          .append("circle")
                          .attr("class","scatterplot_circle")
                          .attr("cx",d=>{return(xScale(d.x))})
                          .attr("cy",d=>{
                            if(isNaN(d.y)){
                              return(yScale(0));
                            };
                            return(yScale(d.y));
                          })
                          .attr("r",3)
                          .attr("fill",d=>{
                            if(isNaN(d.y)){
                              return("black");
                            };
                            return("tomato");
                          })
                          .attr("opacity",0.5)
                          .on("mouseover",tipMouseover)
                          .on("mouseout",tipMouseout);

    // Add Axis
    innerScatterContainer.append("g")
      .attr("transform", "translate(0," + (height+10) + ")")
      .call(d3.axisBottom(xScale))
      .style("opacity",0.4);

    innerScatterContainer.append("g")
        .attr("transform", "translate(-10,0)")
        .call(d3.axisLeft(yScale))
        .style("opacity",0.4);

  }

  render(){
    return <svg id={this.props.id} width={this.props.width} height={this.props.height} style={{background:"whitesmoke",marginTop:3}}/>
  }

}


export default Scatterplot;



