import React from 'react';
import * as d3 from 'd3';

class Histogram extends React.Component{

  constructor(props){
    super(props);
    this.state = {id:this.props.id,value:null};
  }

  componentDidMount()
  {
    if(this.props.data){
      this.draw();
    }
  }

  componentDidUpdate()
  {
    if(this.props.data){
      this.draw();
    }
  }

  draw()
  {
    // Extracting the data for the histogram here.
    // Modify : Make sure that all datatypes can be handled by the histogram.
    const initialhistogramData = []; // data before applying offset to manage negative values.
    this.props.data.forEach(d=>{initialhistogramData.push(parseFloat(d[this.props.parameter])?parseFloat(d[this.props.parameter]):0)});

    // Initializing Histogram Parameters
    const histogramMargin = {top:10, bottom:20, right:20, left:20};
    const width = this.props.width - (histogramMargin.left+histogramMargin.right);
    const height = this.props.height - (histogramMargin.top+histogramMargin.bottom);

    // D3 code to make histogram.
    // Modify : Use appropriate scale, according to the data.


    // Taking care of negative values
    var offset = (d3.min(initialhistogramData)<0)?d3.min(initialhistogramData):0;
    const histogramData = []; // data after applying offset to manage negative values.
    initialhistogramData.forEach((d,i)=>{histogramData.push(d-offset)});

    const xScale = d3.scaleLinear()
            .range([0,width])
            .domain([d3.min(histogramData),d3.max(histogramData)]);

    const bins = d3.histogram()
              .domain(xScale.domain())
              .thresholds(xScale.ticks(20))(histogramData);

    const yScale = d3.scaleLinear()
            .range([height,0])
            .domain([0,d3.max(bins,d=>{return d.length})]);

    const histogramContainer = d3.select("#"+this.props.id);

    // Remove Group holding last visualization
    histogramContainer.select("#Histo").remove();

    const innerhistoContainer = histogramContainer.append("g")
              .attr("id","Histo")
              .attr("transform","translate("+histogramMargin.left+","+histogramMargin.top+")")

    // Append new elements for every bin
    const each_rect = innerhistoContainer.selectAll("g")
                          .data(bins)
                          .enter()
                          .append("g")
                          .attr("class","histogram_rect")
                          .attr("transform", function(d) { return "translate(" + xScale(d.x0) + "," + yScale(d.length) + ")"; })

    each_rect.append("rect")
              .attr("x",1)
              .attr("width",d=>{return xScale(d.x1-d.x0)})
              .attr("height",d=>{return height-yScale(d.length)})
              .style("fill","tomato")
              .style("opacity",0.8)
              .style("stroke","whitesmoke")
              .style("stroke-width",1.0)

    // add the text values on top of every histogram rect
    each_rect.append("text")
            .attr("class","histogram_label")
            .text(d=>{return d.length})
            .attr("x",d=>xScale(d.x1-d.x0)/2)
            .attr("y",0)
            .attr("font-family","sans-serif")
            .attr("font-size","6px")
            .attr("fill","black")
            .attr("text-anchor","middle");

     // add the x Axis

     const xScale_axis = d3.scaleLinear()
             .range([0,width])
             .domain([d3.min(initialhistogramData),d3.max(initialhistogramData)]);

     innerhistoContainer.append("g")
       .attr("transform", "translate(0," + height + ")")
       .call(d3.axisBottom(xScale_axis))
       .style("opacity",0.4);

  }

  render(){
    return <svg id={this.props.id} width={this.props.width} height={this.props.height} style={{background:"whitesmoke",marginTop:7}}/>
  }

}


export default Histogram;



