import React from 'react';
import * as d3 from 'd3';
import '../styles/App.css';
import ParaDropdown from '../components/paradropdown';
import Histogram from '../components/histogram';
import Scatterplot from '../components/scatterplot';
class App extends React.Component {

  constructor(props){
    super(props);
    // Initializing State of the Application
    this.state={data:null,loadError:null, para1:'S. [Fe/H]', para2:'S. Distance (pc)',paraoptions:null};
    this.parametervalue = this.parametervalue.bind(this)
  }

  componentWillMount(){
    // Loading data from the csv file
  d3.csv('./Data/data.csv',(error,data)=>{
    if(error){
      let new_state = this.state;
      new_state.loadError = true;
      this.setState(new_state);
    }
      let new_state = this.state;
      new_state.data = data;
      // If data is read, what are the possible parameters the user can select
      if(this.state.data)
      {
        new_state.paraoptions = Object.getOwnPropertyNames(this.state.data[0]);
      }
      this.setState(new_state);
    });
  }

  render() {

    const width = 960;
    const height_histogram = 150;
    const height_scatterplot = 500;
    const margin = 2;

    return (
      <div id="MainContainer">
        <div id="Header">
          <h1>EXOPLANET DATA EXPLORER</h1>
        </div>
        <div id="ParameterContainer" style={{width:width}}>
          <div id="Parameter1" style={{float:"left",width:width/2 - 2*margin}}>
            <ParaDropdown id="Parameter1_Select" options={this.state.paraoptions} defaultOption={this.state.para1} callbackFromParent={this.parametervalue}/>
            <Histogram id="Parameter1_Histogram" data={this.state.data} parameter={this.state.para1} width={width/2 - 2*margin} height={height_histogram}></Histogram>
        </div>
          <div id="Parameter2" style={{float:"right",width:width/2 - 2*margin}}>
            <ParaDropdown id="Parameter2_Select" options={this.state.paraoptions} defaultOption={this.state.para2} callbackFromParent={this.parametervalue}/>
            <Histogram id="Parameter2_Histogram" data={this.state.data} parameter={this.state.para2} width={width/2 - 2*margin} height={height_histogram}></Histogram>
        </div>
        </div>
        <div id="ScatterplotContainer">
          <Scatterplot id="Scatterplot" data={this.state.data} width={width} height={height_scatterplot} parameter={[this.state.para1,this.state.para2]}></Scatterplot>
        </div>
      </div>
    );
  }

  parametervalue(parameter)
  {
    let new_state = this.state;
    (parameter.id==="Parameter1_Select")?(new_state.para1 = parameter.value):(new_state.para2 = parameter.value);
    this.setState(new_state);
  }
}

export default App;



